@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

.services-section {
  min-height: 100vh;
}

.background-container {
  background-color: #fafafa;
}
:root {
    --primary: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
    white-space: nowrap;
}

.btn--primary {
    background-color: #fff;
    background-color: var(--primary);
    color: #242424;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 18px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--mobile {
    text-align: center;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 24px;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
}

.btn--wide {
    padding: 12px 64px;
    font-size: 20px;
}

.btn-link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 8px 16px;
    height: 100%;
    width: 100%;
  }

.btn--large:hover, .btn--medium:hover, .btn--mobile:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
}

.btn--wide:hover {
    transition: all 0.3s ease-out;
    color: #fff;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.blueGreen {
    background-color: #36ae94;
    color: #fff;
    border: none;
}

.lightGreen {
    background-color: #009e6f;
    color: #fff;
    border: none;
}

.green {
    background-color: #00856a;
    color: #fff;
    border: none;
}

.primary {
    background-color: #003f53;
    color: #fff;
    border: none;
}

.primary:hover {
    background-color: #fff;
    color: #003f53;
}
.home-hero-section {
    color: #fff;
    padding: 150px 0;
    min-height: 100vh;
    scroll-snap-align: start;
    background-color: #fafafa;
}

.home-hero-row {
    align-items: center;
}

.row {
    display: flex;
    margin-right: -15px;
    margin-bottom: -15px;
    margin-left: -15px;
    flex-wrap: wrap;
    align-content: stretch;
}

.col {
    margin-bottom: 15px;
    padding-right: 15px;
    padding-left: 15px;
    flex: 1 1;
    max-width: 50%;
    flex-basis: 50%;
}

.home-hero-text-wrapper {
    max-width: 540px;
    padding-top: 0;
    padding-bottom: 60px;
}

.top-line {
    color: #88c0ad;
    font-size: 16px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    margin-bottom: 16px;
}

.heading {
    margin-bottom: 24px;
    font-size: 48px;
    line-height: 1.1;
    font-weight: 600;
    color: #f7f8fa;
}

.dark {
    color: #1c2237;
}

.darkBg {
    background-color: #003f53;
}

.home-hero-subtitle {
    max-width: 500px;
    margin-bottom: 35px;
    font-size: 18px;
    line-height: 24px;
    text-align: justify;
}

.home-hero-img-wrapper {
    max-width: 555px;
}

.home-hero-img {
    max-width: 95%;
    margin: 0 0 0 10px;
    padding-right: 0;
}

img {
    border: 0;
    max-width: 100%;
    vertical-align: middle;
    display: inline-block;
}

.list {
    display: grid;
    grid-template: "a a"
                   "b b";
}

.list-item {
    display: flex;
    flex-direction: column;
    color: #000;
    width: 200px;
    height: 180px;
    font-size: 14px;
    font-weight: bold;
    background-color: #fafafa;
    justify-content: center;
    text-align: center;
    align-items: center;
    transition: all 0.5s;
    margin-bottom: 50px;
}

.list-image {
    margin-bottom: 20px;
}

/* .list-item:hover{
    transform: scale(1.4) rotate(5deg);
    animation-delay: 2s;
} */

.flat-list-item {
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    margin-left: 20px;
    margin-bottom: 15px;
}

@media screen and (max-width: 951px) {
    .container {
        padding-right: 30px;
        padding-left: 30px;
    }  
    
}

@media screen and (max-width: 850px) {
    .home-hero-text-wrapper {
        padding-bottom: 65px;
    }

    .col {
        max-width: 100%;
        flex-basis: 100%;
    }

    .list {
        display: grid;
        grid-template: "a"
                       "b"
                       "c"
                       "d";
        justify-items: center;
    }
}
.landingContainer {
    background: #003f53;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    height: 100vh;
    position: relative;
    top: -80px;
}

.landingBg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 112%;
    overflow: hidden;
}

.videoBg {
    width: 100%;
    height: 100%;
    object-fit: cover;
    background: #003f53;
}

.landingContent {
    max-width: 1200px;
    position: absolute;
    padding: 8px 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.landingContent h1 {
    color: #fff;
    font-size: 48px;
    text-align: center;

}

.landingContent p {
    margin-top: 24px;
    color: #fff;
    font-size: 24px;
    text-align: center;
    max-width: 600px;
    margin-bottom: 44px;
}

@media screen and (max-width: 768px) {
    .landingContent h1 {
        font-size: 40px;
    }  
    .landingContent p {
        font-size: 22px;
    }
}

@media screen and (max-width: 480px) {
    .landingContent h1 {
        font-size: 32px;
    }
    .landingContent p {
        font-size: 18px;
    }
}
.pricing-section {
    padding: 100px 0 160px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #fafafa;
}

.pricing-container-card {
    background: linear-gradient(45deg, #00856a 0%, #009e6f 100%);
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.2);
    width: 280px;
    height: 580px;
    text-decoration: none;
    border-radius: 4px;
}

.pricing-container-card:nth-child(2) {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    background: #242424;
    margin: 0 24px;
}

.pricing-container-card:hover {
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
    transition: all 0.3s ease-out;
    color: #1c2237;
}

.pricing-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.pricing-wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 auto;
}

.pricing-heading {
    color: #1c2237;
    margin-bottom: 24px;
}

.pricing-container-cardInfo {
    display: flex;
    flex-direction: column;
    height: 500px;
    padding: 24px;
    align-items: center;
    color: #fff;
}

.pricing-container-card h3 {
    margin-bottom: 5px;
    font-size: 24px;
}

.pricing-container-card h4 {
    font-size: 40px;
}

.pricing-container-card p {
    font-size: 14px;
    margin-bottom: 24px;
}

.pricing-container-features {
    margin: 16px 0 32px;
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.pricing-container-features li{
    margin-bottom: 10px;
}

.icon {
    margin: 24px 0;
}

@media screen and (max-width: 960px) {
    .pricing-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    .pricing-container-card {
        width: 90%;
    }

    .pricing-wrapper {
        margin: 0 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .pricing-container-card:nth-child(2) {
        -webkit-transform: scale(1);
                transform: scale(1);
        background: #242424;
        margin: 24px;
    }

    .pricing-container-card:hover {
        -webkit-transform: none;
                transform: none;
    }
}

