.container-services {
    width: 100%;
    height: 100%;
    padding: 0px 8%;
    box-sizing: border-box;
    font-family: 'PT Sans', sans-serif;
    background-color: #fafafa;
}


.container-services h1 {
    text-align: center;
    padding-top: 10%;
    margin-bottom: 60px;
    font-weight: 600;
    position: relative;
}

.container-services h1::after {
    content: '';
    background: #88c0ad;
    width: 100px;
    height: 5px;
    position: absolute;
    bottom: -5px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.row-services {
    display: grid;
    grid-template-columns:repeat(auto-fit, minmax(min(100%/3, max(250px, 100%/5)), 1fr));
    grid-gap: 30px;
}

.service {
    text-align: center;
    padding: 25px 10px;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;
    background: transparent;
    transition: -webkit-transform 0.5s background 0.5s;
    transition: transform 0.5s background 0.5s;
    transition: transform 0.5s background 0.5s, -webkit-transform 0.5s background 0.5s;
}

.service i {
    font-size: 40px;
    margin-bottom: 10px;
    color: #88c0ad;
}

.service h2 {
    font-weight: 600;
    margin-bottom: 8px;
}

.service:hover {
    background: #88c0ad;
    color: #fff;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.service:hover i {
    color: #fff;
}


.pricing-section {
    padding: 100px 0 160px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #fafafa;
}

.pricing-container-card {
    background: linear-gradient(45deg, #00856a 0%, #009e6f 100%);
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.2);
    width: 280px;
    height: 580px;
    text-decoration: none;
    border-radius: 4px;
}

    .pricing-container-card:nth-child(2) {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
        background: #242424;
        margin: 0 24px;
    }

    .pricing-container-card:hover {
        -webkit-transform: scale(1.06);
                transform: scale(1.06);
        transition: all 0.3s ease-out;
        color: #1c2237;
    }

.pricing-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.pricing-wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 auto;
}

.pricing-heading {
    color: #1c2237;
    margin-bottom: 24px;
}

.pricing-container-cardInfo {
    display: flex;
